import React, { Component } from 'react';
import {Polyline} from 'google-maps-react';

class FavoritiesMap extends Component {
	constructor(props) {
		super(props);

		this.mapRef = React.createRef();
		this.trip = null;
		this.bumps = [];
	}

	componentDidMount = () => {
		this.loadMap();
	}

	componentDidUpdate = (prevProps, prevState) => {
		if (prevProps.google !== this.props.google || prevProps.favorities !== this.props.favorities) {
			this.loadMap();
		}
	}

	loadMap = () => {
		if (this.props.google) {

			let maps = this.props.google.maps;
			let { zoom, path, center, bumps } = this.props.favorities;

			const mapOptions = {
				center,
				zoom,
				zoomControl: false,
    			scaleControl: false,
    			fullscreenControl: false,
    			mapTypeControl: false,
    			streetViewControl: false,
			};

			let tripOptions = {
				path,
		        strokeColor: '#4D8FAC',
		        strokeOpacity: 0.8,
		        strokeWeight: 4,
			};

			let bumpOption = {
				path: "M-4,0a4,4 0 1,0 8,0a4,4 0 1,0 -8,0",
				fillColor: "red",
				strokeColor: "red",
                fillOpacity: 1.0,
                scale: 0.4,
                anchor: new maps.Point(50, 50)
			}

			this.map = new maps.Map(this.mapRef.current, mapOptions);
			this.trip = new maps.Polyline(tripOptions);
			
			if (this.map) this.trip.setMap(this.map); 

			bumps.forEach(bump => {
				this.bumps.push(new maps.Marker({ position: {lat: bump.lat, lng: bump.lng}, icon: bumpOption }));
				this.bumps[this.bumps.length - 1].setMap(this.map);
			})

			


		}
	}

	componenWillUnmount = () => {
		this.trip.setMap(null);
	}

	render() {
		console.log(this.props);
		if (!this.props.favorities) return;
		let { width, height } = this.props.dimentions;

		let style = {
			width: width +"px", 
			height: height +"px", 
			position: "relative"
		};

	  	return (
	  		<div ref={this.mapRef} style={style} />
        );
	}
}

export default FavoritiesMap;
