import React, { Component } from 'react';
import '../../styles/report-bumps.css';

import { db } from '../Firebase/index';
import {Polyline, GoogleApiWrapper, Marker} from 'google-maps-react';

import BumpsMap from './bumps-map';
import FavoritiesMap from './favorities-map';
import Car from './car';
import { NextStepButton } from '../Shared/index';

import * as helpers from '../../helpers/index';



let navigatorId;

const NAVIGATOR_OPTIONS = {
	enableHighAccuracy: true,
	timeout: 5000,
	maximumAge: 0
};

class Main extends Component {
	constructor(props) {
		super(props);

	    this.state = {
	    	isDataFetched: false, // обработка загрузки данных
	    	isRecordingMode: false, 
	    	data: [],
	    	currentLocation: {}, 
	    	previousLocation: {}, 
	    	acceleration: {average: 0, max: 0, ticks: 0}, 
	    	//context: null,
	    	zoom: 16, 
	    	angle: 0, 
	    	move: 0, 
	    	dimentions: {
	    		height: window.innerHeight,// * 0.842, //0.764
	    		width: window.innerWidth
	    	},
	    	favorities: [], 
	    	bumps: [],
	    	selectedTrip: null
	    };

	    this.canvas = React.createRef();
	    this.mymap = React.createRef();
	    this.mymarker = React.createRef();
	}

	componentDidMount = () => {
		//const canvas = this.canvas.current;
		//this.setState({ context: canvas.getContext('2d') });

	 // 	var data;
		// db.collection("coverage").get().then(querySnapshot => {
		//     querySnapshot.forEach(function(doc) {
		//     	if (doc.data().data) {
		//     		data = JSON.parse(doc.data().data);
		//     	} else {
		//     		data = [];
		//     	}

		//     });
		// }).then(() => {
		// 	this.setState({ 
		// 		context: canvas.getContext('2d'),
		// 		isDataFetched: true,
	 //  			data 
	 //  		}, () => console.log(this.state.data));
		// });


		this.setState({ // убрать при подключении Firestore
			//context: canvas.getContext('2d'),
			isDataFetched: true,
			data: [] //set test for testing purposes
		});

		// window.addEventListener('load', function() {
		//   window.history.pushState({ noBackExitsApp: true }, '')
		// })

		// window.addEventListener('popstate', function(event) {
		//   if (event.state && event.state.noBackExitsApp) {
		//     window.history.pushState({ noBackExitsApp: true }, '')
		//   }
		// })

		window.addEventListener('resize', this.updateWindowDimensions);
	}

	componentDidUpdate = (prevProps, prevState) => {
	   //
	}

	componentWillUnmount = () => {
		window.removeEventListener('resize', this.updateWindowDimensions);
	}

	updateWindowDimensions = () => {
	  this.setState({ 
	  	dimentions: {
	    		height: window.innerHeight,// * 0.764,
	    		width: window.innerWidth
	    	} 
	    });
	}

	toggleRecording = () => {
		if (!this.state.isRecordingMode) {
			this.startRecording();
		} else {
			this.stopRecording();
		}
		
		this.setState({isRecordingMode: !this.state.isRecordingMode});  
	}

	startRecording = () => {
		this.setState({
			data: []
		});

		if (navigator && navigator.geolocation) { // обработка отсутствия
			navigatorId = navigator.geolocation.watchPosition(this.updateLocationData, this.err, NAVIGATOR_OPTIONS);
	    }	

	    if (window.DeviceMotionEvent) { //обработка отсутствия
	    		window.addEventListener("devicemotion", this.updateAccelerationData); 
	    }
	}

	err = () => {
		console.log("error");
	}

	updateAccelerationData = (ev) => {
		let { x, y, z } = ev.acceleration;
		let { average, max, ticks } = this.state.acceleration;
		let { lat, lng } = this.state.currentLocation;
		let bumps = this.state.bumps;		

		if (x !== null && y !== null && z !== null) {
			let curr = Math.pow((x * x + y * y + z * z), 1 / 3); //current level of acceleration with existing phone position

			if (curr > average * 1.2) {
				average = (average * ticks + curr )/ (ticks + 1);
				this.setState({
					bumps: (bumps.length === 0 || !helpers.compare(bumps[bumps.length - 1], {lat, lng})) ? [...this.state.bumps, {lat, lng}] : bumps,
					acceleration: {average, max, ticks: ticks++ }
				})
			}
		}
	}	

	updateLocationData = location => {
		var prev = this.state.currentLocation;
		let data = this.state.data;

		var lat = location.coords.latitude;
		var lng = location.coords.longitude;
		var timestamp = location.timestamp;

		var distance = helpers.getDistance(prev.lat, prev.lng, lat, lng); // в метрах
		var angle = helpers.getAngle(prev.lat, prev.lng, lat, lng);
		var time = Math.abs(prev.timestamp - timestamp)/ 1000; // в секундах
		var speed = (distance/time) ? (distance/time * 3600 / 1000) : 0; //в км/час

		this.setState({	
			previousLocation: prev,
			currentLocation: { lat,	lng, speed,	timestamp },
			data: (data.length === 0 || !helpers.compare(data[data.length - 1], {lat, lng})) ? [...this.state.data, {lat, lng}] : data,
			angle: (Math.abs(angle - this.state.angle) > 22) ? angle : this.state.angle,
			move: this.state.move + distance
		});
	}

	updateResultingData = () => {
		
	}

	stopRecording = () => {
		console.log("stopped");

		let bounds = helpers.getBounds(this.state.data);
		let center = helpers.getCenter(bounds);
		let { ne, sw } = bounds;

		var distance = helpers.getDistance(sw.lat, sw.lng, ne.lat, ne.lng);
		var zoom = helpers.getZoom(distance);

		navigator.geolocation.clearWatch(navigatorId);
		window.removeEventListener("devicemotion", this.updateAccelerationData);

		if (this.state.data.length <= 1) return;

		this.setState({favorities: [...this.state.favorities, {path: this.state.data, zoom, center, bumps: this.state.bumps}]});

		//db.collection("coverage").doc(topleft).set({data: JSON.stringify(this.state.data)});

		// docRef.get().then(function(doc) {
		//     if (doc.exists) {
		//         console.log("Document data:", doc.data());
		//     }
		// });
	}

	resetMove = () => {
		this.setState({
			move: 0
		})
	}

	onZoomChanged = (mapProps, map, clickEvent) => {
		console.log(map.getZoom());
	}

	sendAccelToFirebase = () => {
		//var docRef = db.collection('limit').doc('result');
		//docRef.set({data: this.state.testaccel});
	}

	selectTrip = i => {
		this.setState({selectedTrip: i})
	}

	render() {
		let { bumps, favorities, selectedTrip, dimentions, isRecordingMode, currentLocation } = this.state;
		let google = this.props.google;
		let tripMap, trips = <div>No data</div>;

		let bumpsMap = bumps.map(bump => (<Marker position={{lat: bump.lat, lng: bump.lng}} icon = {{
				path: "M-4,0a4,4 0 1,0 8,0a4,4 0 1,0 -8,0",
				fillColor: "red",
				strokeColor: "red",
                fillOpacity: 1.0,
                scale: 0.4,
                anchor: new google.maps.Point(50, 50)}} />));

		if (favorities.length) { 
			trips = favorities.map((trip, i) => (<div onClick={()=> this.selectTrip(i)} key={i} className="temp-button">{i}</div>));
		} 		

		if (favorities.length && selectedTrip !== null) tripMap = <FavoritiesMap google={google} favorities={favorities[selectedTrip]} dimentions={dimentions} />
		
		if (this.props.isFavoritiesView) {
			return (
				<div className="map favorities-map">
					<div className="favorities-list">{trips}</div>
					{tripMap}								
				</div>	
			);
		}

		return (
			<div className="map">
				<BumpsMap 
					centerAroundCurrentLocation
					google={google}
					updateLocationData={this.updateLocationData}
					currentLocation={currentLocation}
					isRecordingMode={isRecordingMode}
					onZoomChanged={this.onZoomChanged}		
					dimentions={dimentions}
					camelize={helpers.camelize}						
					>
					<Car 
						angle={this.state.angle}
						move={this.state.move}
						resetMove={this.resetMove}
						/>						
					<Polyline
						path={this.state.data}
						strokeColor="#4D8FAC"
						strokeOpacity={0.8}
						strokeWeight={4} 
						/>
					{bumpsMap}
				</BumpsMap>
				<NextStepButton 
					color={!isRecordingMode ? "#757d75" : "#e34929"}
					toggleButton={this.toggleRecording}
					disableCondition={!this.state.isDataFetched}
					text={isRecordingMode ? "Stop Recording" : "Start Recording"}
					/>	
			</div>
		);
	}
}

export default GoogleApiWrapper({
	apiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY
})(Main);